import React from "react";

const projects = [
  {
    title: "Sales Dashboard",
    description: "A Power BI dashboard analyzing regional sales trends, customer segmentation, and revenue insights.",
    tools: ["Power BI", "DAX", "Excel"],
    image: "./assets/sales_dashboard.webp",
    link: "#"
  },
  {
    title: "Python Forecasting App",
    description: "Time-series forecasting model for sales prediction using ARIMA and Prophet, deployed via Streamlit.",
    tools: ["Python", "Prophet", "Streamlit"],
    image: "./assets/forecast_app.webp",
    link: "#"
  },
  {
    title: "Marketing Analytics",
    description: "SQL-based data pipeline and Tableau dashboard for visualizing campaign ROIs and customer behavior.",
    tools: ["SQL", "Tableau", "ETL"],
    image: "./assets/marketing_analytics.webp",
    link: "#"
  }
];

const Portfolio = () => {
  return (
    <section id="portfolio" className="portfolio-section">
      <div className="portfolio-header">
        <h2>Portfolio</h2>
        <p>A collection of my recent analytics and data storytelling projects.</p>
      </div>

      <div className="portfolio-description">
        <p>
          Here are some of the projects I have worked on. Each one showcases my expertise in data analysis, visualization, and storytelling. I aim to deliver actionable insights that drive business success.
        </p>
      </div>

      <div className="portfolio-grid">
        {projects.map((project, index) => (
          <div className="project-card" key={index}>
            <img src={project.image} alt={project.title} className="project-image" />
            <div className="project-content">
              <h3 className="project-title">{project.title}</h3>
              <ul className="project-tags">
                {project.tools.map((tool, i) => (
                  <li key={i}>{tool}</li>
                ))}
              </ul>
              <p className="project-description">{project.description}</p>
              <a href={project.link} className="project-link">View Project â†’</a>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Portfolio;
